//所有配置配置好了 在main进行使用
//导入挂载使用  会找到相对路径地址的index 或相对路径下的带后缀文件
import Vue from 'vue' //导入框架
import App from './App.vue' //导入APP.vue资源   留给render：使用方法进行渲染
//import '../src/plugins/element.js'  //导入按需求编辑好的element vue框架“要素”包
import ElementUI from 'element-ui'  //导入框架
import 'element-ui/lib/theme-chalk/index.css' //导入框架ui组件
import 'font-awesome/css/font-awesome.min.css'  //导入图标包
import axios from 'axios'   //导入Axios 是一个基于 promise 的 HTTP 库(js:方法or函数)；以发送get、post请求 配个路由使用
import router from './router' //导入router 路由器 网络协议  配合路由使用
import service from './service'
import echarts from 'echarts'
import echartsGL from 'echarts-gl'


Vue.use(ElementUI)  //Vue.use()的作用是通过全局方法 Vue.use() 使用插件
Vue.prototype.axios =axios //已被封装  挂载原型,可以全局使用 prototype表示该函数的原型  全局变量
Vue.prototype.service=service
Vue.prototype.$echarts=echarts
Vue.prototype.$echartsGL=echartsGL




Vue.config.productionTip = false
//最终执行给静态页面显示效果   流程 挂载路由  路由实例Home.vue组件并重定向地址 render将APP.vue实例并挂载  $mount将new new效果挂载到静态页面方法#id为app的元素上

//路由导航守卫
/**beforeEach 守卫是异步解析执行，此时导航在所有守卫 resolve 完之前一直处于 等待中。
 每个守卫方法接收三个参数：
 to: Route: 即将要进入的目标路由对象
 from: Route: 当前导航正要离开的路由
 next: Function: 一定要调用该方法来 resolve 这个钩子。执行效果依赖next方法的调用参数。确保要调用next方法，否则钩子就不会被 resolved*/
router.beforeEach((to,from,next)=>{
  if(!localStorage.getItem('username')){    //判断是否有缓存 没有为true  证明没有登录过
    if(to.path!=='/login'){       //当要进入的路由绝对不为指定路由(login)时 为true 为login时 放行
      next('/login')  //跳转 登录页
    }else next()
  }next()   //放行
})


new Vue({
  router,                  //挂载路由
  render: h => h(App),    //挂载组件(根据传进来的组件创建元素) 箭头函数的写法,将App那个vue组件渲染成DOM节点 h(APP)形参 APP：传进的组件名
}).$mount('#app') //该方法是直接挂载到入口文件index.html 的 id=app 的dom 元素上的 等同于el:"#app",

/*
render?(
    this: undefined,
    createElement: CreateElement,
    context: RenderContext<Props>
  ): VNode | VNode[]
render函数的作用
将App那个vue组件渲染成DOM节点。render函数里面的形参，可以做到这一点，所以 h这个函数 ， 里面传进去一个App组件。
注意： h 代表的是createElement
createElement() 方法通过指定名称创建一个元素
以下是具体的写法：
render: function (createElement) {
    return createElement(App);
}

配置el属性：
new Vue({
 el:"#app",
 router
});

如果没有配置el属性，可以使用手动挂载$mount("#app")
new Vue({
 router
}).$mount('#app');

mount(’#app’) ：手动挂载到id为app的dom中的意思当Vue实例没有el属性时，则该实例尚没有挂载到某个dom中；假如需要延迟挂载，可以在之后手动调用vm.$mount()方法来挂载
该方法是直接挂载到入口文件index.html 的 id=app 的dom 元素上的
*/

