//router路由配置
//vue 路由使用方法
import Vue from 'vue'
import Router from 'vue-router'     //路由器 网络协议
//import Home from '../components/Home'   //导入主页Home组件  用于注册

Vue.use(Router)     //Vue.use()的作用是通过全局方法 Vue.use() 使用插件


//通过路由将地址指向home 更改component文件下的Home指向后的指向地址 也就是路由到指向地址
//用于重定向404之类 配合返回get post库可以获取200 404等进行“响应or需求”操作
//     difoalt
//继承   默认的   新  路由
export default new Router({

    routes:[
        {
            path:'/',
            redirect:'/login',
            hidden:true,
            component:()=>import('@/components/Login')
        },
        {
            path:'/login',
            name:'Login',
            hidden:true,
            component:()=>import('@/components/Login')
        },
        {
            path:'*',
            name:'NotFound',
            hidden:true,
            component:()=>import('@/components/NotFound')
        },
        {
            path:'/home',
            name:'学生管理',
            iconClass:'fa fa-users',
            redirect:'/home/student',
            component:()=>import('@/components/Home'),
            children:[
                {
                    path:'/home/student',
                    name:'学生列表',
                    iconClass:'fa fa-list',
                    component:()=>import('@/components/students/StudentList')
                },
                {
                    path:'/home/info',
                    name:'信息列表',
                    iconClass:'fa fa-list-alt',
                    component:()=>import('@/components/students/InfoList')
                },
                {
                    path:'/home/infos',
                    name:'信息管理',
                    iconClass:'fa fa-list-alt',
                    component:()=>import('@/components/students/InfoLists')
                },
                {
                    path:'/home/Work',
                    name:'作业列表',
                    iconClass:'fa fa-list-ul',
                    component:()=>import('@/components/students/WorkList')
                },
                {
                    path:'/home/Works',
                    name:'作业管理',
                    iconClass:'fa fa-th-list',
                    component:()=>import('@/components/students/WorkMent')
                },
            ]
        },
        {
            path:'/home',
            name:'数据分析',
            iconClass:'fa fa-bar-chart',
            component:()=>import('@/components/Home'),
            children:[
                {
                    path:'/home/dataview',
                    name:'数据概览',
                    iconClass:'fa fa-line-chart',
                    component:()=>import('@/components/dataAnalysis/DateView')
                },
                {
                    path:'/home/mapview',
                    name:'地图概览',
                    iconClass:'fa fa-line-chart',
                    component:()=>import('@/components/dataAnalysis/MapView')
                },
                {
                    path:'/home/travel',
                    name:'旅游地图',
                    iconClass:'fa fa-line-chart',
                    component:()=>import('@/components/dataAnalysis/TravelMap')
                },
                {
                    path:'/home/score',
                    name:'分数地图',
                    iconClass:'fa fa-line-chart',
                    component:()=>import('@/components/dataAnalysis/ScoreMap')
                },
                {
                    path:'/home/MC',
                    name:'我的世界',
                    iconClass:'fa fa-line-chart',
                    component:()=>import('@/components/dataAnalysis/MCmodelMap')
                },
                {
                    path:'/home/weibo',
                    name:'微博热搜',
                    iconClass:'fa fa-line-chart',
                    component:()=>import('@/components/dataAnalysis/weiboMap')
                },
                {
                    path:'/home/code',
                    name:'待定义',
                    iconClass:'fa fa-line-chart',
                    component:()=>import('@/components/dataAnalysis/codeMap')
                }
            ]
        },
        {
            //因为只是一个导航栏，默认渲染主页
            path:'/home',
            name:'用户中心',
            iconClass:'fa fa-users',
            //子路由注册的组件渲染到home页面的路由出口处
            component:()=>import('@/components/Home'),
            //点击子路由
            children:[{
                //访问的路径
                path: '/home/user',
                name:'权限管理',
                iconClass: 'fa fa-user',
                //注册组件
                component:()=>import('@/components/users/User')
            }]
        },
    ],
    mode: 'history',
})
/*
   {
       path:'/home',     //指向地址 路由重定向后 访问的路径
       //注册导入的Home.vue文件 两种导入方式，直接导入组件 变量定义导入组件   组件的注册 注册导入的主页组件 全局注册组件，可以使用Vue.component(tagName, options)
       //官方推荐路由懒加载写法()=>等同于 function(){return import('../components/Home')}
       //component:()=>import('@/components/Home')         懒加载
       //component:resolve => require(['/foo'], resolve)   异步组件加载 resolve函数：模块引入后的回调函数, 接收一参引入的模块作为参数, 引入多个模块时以多参而非数组形式传递
       //resolve (注意 resolve 只是形参) 是 vue-router 返回的方法, 只执行一次, 用于(挂载组件?), 接收 require 引入的模块为参数
       //require 的一参为要引入的模块, 为多个时用数组形式传入, 二参为引入完成后的回调函数, 该回调函数的参数为一参引入的模块, 多个模块时为多个参数而不是数组
       component:resolve=>require(['@/components/Home'],resolve),      //resolve回调返回数据后在继续执行下面的代码
   }
*/
/*
懒加载：又叫延时加载，即在需要的时候进行加载，随用即载
使用懒加载的原因：
像vue这种（spy）单页面应用，如果没有使用到懒加载，webpack打包的文件过大，造成进入首页时，加载的资源过多，时间过长。
即使做了loading也不利于用户体验，而运用懒加载可以将页面进行划分，需要的时候加载页面，可以有效的分担首页所承担的加载压力，减少首页加载事件，简单来说就是进入首页不用一次加载过多资源造成时间过长。
当使用 resolve 懒加载方式则可以将页面进行划分，webpack 打包时会将 component 分别打包成不同的js。只有访问这个路由网址时才会加载这个js。
改写为以下形式也可执行
const router = new VueRouter({
    routes: [{
        path: '/foo',
        component: mount => require(['/foo'], (mod) => {mount(mod);})
    }]
})
---------
以下是resolve函数 异步回调使用方法 执行玩了在执行then里的方法 resolv返回的value传递给then参
let p = new Promise((resolve,reject)=>{
    setTimeout(()=>{
        //console.log('脱完衣服');
        resolve(3);
    },3000)
});
p.then((d)=>{
    console.log('去洗'+d+'件衣服');
});
*/

/**component详解*/
/*
当你使用component属性时，router会通过你赋给该属性的值，使用React.createElement方法去创建一个新的React元素，
这意味着如果你给component属性赋的值是一个内联函数，那他每次渲染都会创建一个新的组件，这会导致每次渲染都会伴随新组件的挂载和旧组件的卸载，而不是只是对已存在组件的更新操作。
*/

